body {
  background: #faf9f9 !important;
}

.todos-container {
  background: #add2c9;
  padding: 20px 0;
}

.title {
  margin: 20px;
}

hr {
  width: 25%;
}

.todo,
.add-div {
  width: 75%;
  margin: 10px auto;
}

.todo-inside,
.add-div-inside {
  width: 100%;
  display: grid;
  grid-column-gap: 5px;
  grid-template-columns: 1fr auto;
  align-items: center;
}

.btn:focus,.btn:active, input {
  box-shadow: none !important;
}

.add-div-inside div *{
  margin: 0 3px;
}

.todo-inside button {
  height: max-content;
  margin: 0 3px;
}

.low-priority-div{
  background: #5ea3a3;
}

.high-priority-div{
  background: #c11327d5;
  color: #000;
}

.todo-content{
  height: max-content !important;
  padding: 3px;
}

.todo-content .completed{
  text-decoration: line-through;
}

.todos-empty-title {
  margin: 20px 0;
}

.todo .fa-pen {
  color: #faf9f9;
}

.todos-container-buttons button {
  margin: 10px;
}

.quote-box {
  width: 75%;
  background: #faf9f9;
  margin: auto;
}

@media only screen and (max-width: 600px) {
  .todo-inside,
  .add-div-inside {
    grid-template-rows: repeat(2, auto);
    grid-template-columns: auto;
    grid-row-gap: 5px;
  }
}